import React from 'react';
import { Button, Col, Row } from 'reactstrap';
import { Card, Image } from 'react-bootstrap';

const ShopItem = ({ product, onClick }) => (
    <div className="shop-item-card h-100">
        <Card className="h-100">
            <Card.Body>
                <Image src={product.image} width="75%" height="150px"/>
                <Card.Text>
                    {product.name}
                </Card.Text>
            </Card.Body>
            <Card.Footer>
                <Row>
                    <Col className="price-col font-weight-bold my-auto">
                        {`Price: ${product.price} $`}
                    </Col>
                    <Col className="add-to-cart-col">
                        <Button onClick={onClick}>
                            Add to cart
                        </Button>
                    </Col>
                </Row>
            </Card.Footer>
        </Card>
    </div>
);

export default ShopItem;
