import React, {useEffect, useState} from 'react';
import Shop from "../Shop";
import {
    TiShoppingCart,
} from 'react-icons/all';
import {Row,Col, Badge, Button} from "reactstrap";
import {connect} from "react-redux";
import {useHistory, withRouter} from 'react-router-dom';

const LandingPage = ({items, cart}) => {
    const history = useHistory();
    const [cartItemsAmount, setCartItemsAmount] = useState(0);

    useEffect(() => {
        if(items){
            let countItems = items.length;
            setCartItemsAmount(countItems);
            console.log(cart);
        }

    }, [items]);

    return (
        <div className="landing-page">
            <Row>
                <Col md={{size:8, offset: 2 }}>
                    <h1 style={{textAlign: 'center', color: 'white'}}>Welcome to Haris shoe shop</h1>
                </Col>
                <Col style={{textAlign: 'right'}} md={2}>
                    <Button
                        className="shopping-cart-button"
                             onClick={() => history.push('/checkout', cart.price)}>
                    <TiShoppingCart size={40} color="white" style={{marginRight: '38px'}}/>
                    {!!items.length &&
                    <Badge>{cartItemsAmount}</Badge>
                    }
                    </Button>
                </Col>
            </Row>
            {items &&
            <Row>
                <Col>
                    <Shop/>
                </Col>
            </Row>
            }
        </div>
    )
}

export default connect((state) => ({
    items: state.shop.cart.items,
    cart: state.shop.cart,
}) , null)(withRouter(LandingPage));
