import initialState from '../../initialState';
import * as constants from './constants';

export default function reducer(state = initialState.shop, action = {}) {
    const { type, payload } = action;
    switch (type) {
        case constants.ADD_ITEM_TO_CART_SUCCESS: {
            const cartItemsCopy = [...state.cart.items];
            const cartCopy = { ...state.cart };
            const indexOfItem = state.cart.items.findIndex(item => item.id === payload.item.id);
                cartItemsCopy.push({
                    ...payload.item,
                    quantity: 1,
                });
            const totalOrderAmount = cartItemsCopy.map(item => item.price).reduce((prev, next) => prev + next);
            return {
                ...state,
                cart: {
                    ...cartCopy,
                    items: cartItemsCopy,
                    price: totalOrderAmount,
                },
            };
        }

        case constants.REMOVE_ITEM_FROM_CART_SUCCESS: {
            const cartItemsCopy = [...state.cart.items];
            const cartCopy = { ...state.cart };
            const indexfOfElement = cartItemsCopy.findIndex(item => item.id === payload.item.id);
            cartItemsCopy.splice(indexfOfElement, 1);
            const newTotalOrderAmount = cartCopy.order_amount - payload.item.total_amount;
            return {
                ...state,
                cart: {
                    ...state.cart,
                    items: cartItemsCopy,
                    order_amount: newTotalOrderAmount,
                },
            };
        }

        default:
            return state;
    }
}
