import * as constants from './constants';

export const addItemToCartSuccess = (item) => ({
    type: constants.ADD_ITEM_TO_CART_SUCCESS,
    payload: {
        item,
    },
});

export const addItemToCartFail = () => ({
    type: constants.ADD_ITEM_TO_CART_FAIL,
});

export const removeItemFromCartSuccess = (item) => ({
    type: constants.REMOVE_ITEM_FROM_CART_SUCCESS,
    payload: {
        item,
    },
});

export const removeItemFromCartFail = (item) => ({
    type: constants.REMOVE_ITEM_FROM_CART_FAIL,
    payload: {
        item,
    },
});

