import React, {useEffect, useState} from 'react';
import { Col, Row } from 'reactstrap';
import ShopItem from "../ShopItem";
import {retrieveProducts} from "../../services";
import {toastr} from "react-redux-toastr";
import shoe1 from '../../images/shoe10.jpeg';
import shoe2 from '../../images/shoe2.jpeg';
import shoe3 from '../../images/shoe3.jpeg';
import shoe4 from '../../images/shoe4.jpeg';
import shoe5 from '../../images/shoe5.jpg';
import shoe6 from '../../images/shoe6.jpeg';
import shoe7 from '../../images/shoe2.jpeg';
import shoe8 from '../../images/shoe4.jpeg';
import shoe9 from '../../images/shoe9.jpeg';
import shoe10 from '../../images/shoe10.jpeg';
import {addItemToCart} from "../../redux/shop/actions";
import {connect} from "react-redux";

const Shop = ({addItemToCart, items, cart}) => {
    const [products, setProducts] = useState([]);
    let images = [shoe1,shoe2,shoe3,shoe4, shoe5,shoe6, shoe7, shoe8, shoe9, shoe10];
    const [shopCart, setShopCart] = useState(items);

    useEffect(() => {
        (async () => {
            let result = [];
            const {data} = await retrieveProducts();
            console.log(data);
            data.map((product, index) => {
                const prod = {
                    image: images[index],
                    ...product
                }
                result.push(prod);
            })

            setProducts(result);
        })();
    }, []);

    useEffect(() => {
        setShopCart(cart);
    }, [cart]);

    const addToCart = (product) => {
        toastr.success('Successfully added to cart');
        addItemToCart(product);
    };

    return (
        <Row className="m-4">
            {products ? products.map((product, index) => (
                <Col key={index} md={4}>
                    <ShopItem
                        product={product}
                        onClick={() => addToCart(product)}
                    />
                </Col>
            )):
                <Col>
                    <h2>nemaa</h2>
                </Col>
            }
        </Row>
    );
};

export default connect((state) => ({
    items: state.shop.cart.items,
    cart: state.shop.cart,
}) , { addItemToCart })(Shop);